.header {
  padding-top: 40px;
  padding-bottom: 92px;
  height: 230px;
  position: relative;
  margin: 0 auto;
  // margin-bottom: 60px;

  @media screen and (min-width: 768px) {
    padding-bottom: 80px;
    height: 216px;
  }

  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url('/src/images/image-header/abstract-background-mobile.jpg');
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url('/src/images/image-header/abstract-background-mobile2x.jpg');
  }

  @media screen and (min-width: 768px) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url('/src/images/image-header/abstract-background-tablet.jpg');

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
        url('/src/images/image-header/abstract-background-tablet2x.jpg');
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url('/src/images/image-header/abstract-background-desktop.jpg');

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
        url('/src/images/image-header/abstract-background-desktop2x.jpg');
    }
  }
}
.container {
  min-width: 320px;
}
.header__container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;

  @media screen and (min-width: 480px) {
    max-width: 480px;
  }
  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-right: 32px;
    padding-left: 32px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 1280px;
  }
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav__list {
  display: flex;
  margin-left: auto;
  align-items: center;

  & :not(:first-child) {
    margin-left: 39px;
  }
}

.nav__link {
  position: relative;
  height: 14px;
  text-decoration: none;
  font-size: 12px;
  line-height: 1.16;
  font-weight: 500;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    color: #ff6b01;
  }
}

.current::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -3px;
  width: 100%;
  height: 3px;
  background-color: #ff001b;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.logo__link {
  display: flex;
  align-items: center;
}

.logo__text {
  @media screen and (max-width: 767px) {
    display: none;
  }

  @media screen and (min-width: 768px) {
    margin-left: 8px;
    color: #ffffff;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 1.17;
  }
}

.search {
  max-width: 100%;
  display: flex;
  margin-top: 54px;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: 768px) {
    margin-top: 40px;
  }
}

.search__form {
  position: relative;
}

.search__input {
  display: flex;
  align-items: center;
  padding-bottom: 4px;
  padding-right: 20px;
  background-color: transparent;
  border: none;
  width: 280px;
  height: 24px;
  outline: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: #ffffff;
  border-bottom: 0.5px solid #ffffff;

  @media screen and (min-width: 768px) {
    width: 336px;
  }

  @media screen and (min-width: 1280px) {
    width: 394px;
  }

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.17;
    display: flex;
    align-items: center;
    color: #ffffff;
  }
}

.search__btn {
  position: absolute;
  top: 0;
  right: 0;
  padding: 1px 4px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border-radius: 50%;
  border: none;
  outline: none;
}
.nav__auth {
  margin-left: 24px;
}

.modal-open-btn {
  padding: 0;
  background-color: transparent;
  border: none;
}

.modal-open-btn__icon {
  fill: #ffffff;

  &:hover,
  &:focus {
    fill: #ff6b01;
  }
}

.warning__message {
  margin: 20px auto 0;
  width: 280px;
  padding-right: 30px;
  color: #ff001b;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  display: flex;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: 768px) {
    width: 393px;
    padding: 0;
    margin-top: 20px 0 0;
  }
}
